@use './globals';

.no-scroll {
  overflow: hidden;
}

.header-container {
  margin-top: 20px;

}

.header-navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-item {
  margin-right: 16px;
}

.header-link {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;

  font-weight: 500;
  font-size: 12px;
  line-height: normal;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: $primery-text-color;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

}

.header-link:hover,
.header-link:focus {
  color: globals.$accent-color;
}

.logo {
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.125;
  letter-spacing: -0.72px;

  color: globals.$accent-color;
}

.header-logo {
  color: globals.$primery-text-color;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);


}

.header-logo:hover,
.header-logo:focus {
  color: globals.$accent-color;
}

.btns-wrap {
  display: flex;
  align-items: center;
  gap: 14px;
}

.buy-btn {
  border: none;
  background: none;
  padding: 0;
  fill: transparent;
  stroke: globals.$primery-text-color;
  width: 24px;
  height: 24px;
  transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.buy-btn:hover,
.buy-btn:focus {
  stroke: globals.$accent-color;
}

/* ...............TOGGLE BTN......... */
.switch {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #cecdcd;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 18px;
  width: 18px;
  left: 1px;
  bottom: 1px;
  background-color: globals.$primery-background-color;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: globals.$accent-color;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 375px;
    margin-left: auto;
    margin-right: auto;
  }
  .header-list {
    display: none;
  }
  .toggle-switch {
    display: none;
  }
  .slider {
    border: 1px solid #cecdcd;
  }
  /*.............................MOBILE MENU.......................*/
  .menu-open-btn {
    background-color: transparent;
    border: none;
    padding: 0;
    width: 32px;
    height: 32px;
    stroke: globals.$primery-text-color;
    transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .menu-open-btn:hover,
  .menu-open-btn:focus {
    stroke: globals.$accent-color;
  }

  .mobile-menu {
    display: flex;
    flex-direction: column;

    position: absolute;
    top: -101vh;
    left: 50%;

    z-index: 1;

    max-width: 335px;
    height: 100vh;
    width: 75%;

    overflow-y: auto;
    background-color: globals.$accent-color;

    transform: translateX(-50%);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .mobile-menu.is-open {
    transform: translate(-50%, 101vh);
  }

  .menu-close-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 20px;
    right: 20px;

    stroke: globals.$primery-background-color;
    border: none;
    background: none;
  }
  .mobile-menu-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    margin-top: 200px;
  }

  .mobile-menu-link {
    display: block;
    font-family: Inter;
    font-size: 12px;
    font-weight: 400;
    color: $primery-background-color;
  }

  .toggle-switch-mobile {
    position: absolute;
    bottom: 20px;
    left: 20px;
  }
}

@media screen and (min-width: 768px) {
  .header-container {
    margin-top: 10px;
  }


  .header-list {
    display: flex;
  }
  .logo {
    font-size: 18px;
    line-height: 1.1;
  }
  .menu-open-btn,
  .mobile-menu {
    display: none;
  }
  .btns-wrap {
    gap: 18px;
  }

  .slider:before {
    left: 1px;
    bottom: 1px;
  }
}

@media screen and (min-width: 1200px) {
  .header-container {
    margin-top: 20px;
  }
  .btns-wrap {
    gap: 24px;
  }
}

.current {
  color: globals.$accent-color;
  @media screen and (max-width: 768px) {
    font-weight: 500;
    color: globals.$secondary-text-color;
  }
}
